import {
    EditorButton,
    LimelTextEditorCustomEvent,
} from '@limetech/lime-elements';
import { Component, h, State } from '@stencil/core';
/**
 * Example with custom menu
 */
@Component({
    tag: 'limel-example-text-editor-with-custom-menu',
    shadow: true,
})
export class TextEditorWithCustomMenuExample {
    @State()
    private text: { html: string } = { html: '' };

    public render() {
        const menuItems: EditorButton[] = [
            'strong',
            'emphasis',
            'heading 1',
            'heading 2',
            'heading 3',
            'undo',
            'redo',
            'bullet list',
            'ordered list',
            'block quote',
            'join above',
            'lift out',
        ];

        return [
            <limel-text-editor
                onChange={this.handleChange}
                menuItems={menuItems}
            />,
            <limel-example-value value={this.text} />,
        ];
    }

    private handleChange = (
        event: LimelTextEditorCustomEvent<{ html: string }>,
    ): void => {
        event.stopPropagation();
        this.text = event.detail;
    };
}
